function onCity (name, div) {
    if (div) {
        for (let index = 0; index < name.length; index++) {
            let city = name[index]
            city = document.createElement('p');
            city.innerHTML = name[index]
            div.appendChild(city);
        }
        if (name.length > 8 && name.length < 24) {

            // div.classList.add('two')
        }
        if (name.length > 25 && div == all) {

            div.classList.add('for')
        }
    }
}

const _clasters = document.querySelector('.clasters')
const _clasterContent = document.querySelectorAll('.claster-content')
const _clasters__img = document.querySelectorAll('.clasters__img')

_clasterContent.forEach(element => {
    const attrContent = element.getAttribute('claster-content')
    if (element.classList.contains('claster-block')) {
        _clasters__img.forEach(el => {
            const attrImg = el.getAttribute('claster-open')
            if (attrContent == attrImg) {
                el.classList.add('clasters__img-close')
            }
        });
    }
});

_clasters.addEventListener('click', e => {
    const attr = e.target.getAttribute('claster-open')

    _clasterContent.forEach(element => {
        element.classList.remove('claster-block')
        const attrContent = element.getAttribute('claster-content')
        if (element.classList.contains('claster-block')) {
            console.log(element);
            element.classList.remove('claster-block')
        }

        if (attrContent == attr) {
            element.classList.add('claster-block')
        }
        _clasters__img.forEach(el => {
            const attrImg = el.getAttribute('claster-open')
            if (attrContent == attrImg) {
                el.classList.remove('clasters__img-close')
            }
        });


    });

})











